




































































import Vue from "vue";
import AccountBase from "@/components/account/AccountBase.vue";
import { rules } from "@/constants";
export default Vue.extend({
  data: () => ({
    activeModel: {
      userName: "",
      code: "",
    },
    rules: rules,
  }),
  mounted() {
    this.activeModel.userName = this.$route.query.userName.toString();
  },
  components: {
    AccountBase,
  },
});
